body, body * {
  vertical-align: top;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Gill Sans, Gill Sans MT, Calibri, Trebuchet MS, sans-serif;
}

header {
  text-align: center;
  min-height: 72px;
  background-color: #269570;
  justify-content: center;
  align-items: center;
  padding: 36px;
  display: flex;
}

h2 {
  font-size: 36px;
}

main {
  width: 100%;
  justify-content: center;
  display: flex;
}

.container {
  margin-top: 72px;
}

.form {
  min-width: 480px;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 52px;
  display: flex;
}

.form__row {
  flex-direction: column;
  gap: 16px;
  font-size: 24px;
  display: flex;
}

.form__input {
  max-width: 460px;
  max-height: 320px;
  border: 1px solid;
  border-radius: 3px;
  padding-left: 6px;
  font-size: 18px;
  line-height: 1.8;
}

.add__btn {
  width: 72px;
  color: #fff;
  background-color: #269570;
  border-radius: 3px;
  align-self: flex-end;
  padding: 8px 12px;
  font-size: 18px;
}

.add__btn:hover {
  cursor: pointer;
}

.sort__menu {
  gap: 16px;
  padding-bottom: 24px;
  display: flex;
}

.sort__menu button {
  color: #fff;
  background-color: #269570;
  border-radius: 3px;
  padding: 4px;
}

.sort__menu button:hover {
  cursor: pointer;
}

.list {
  flex-direction: column;
  gap: 12px;
  padding-bottom: 56px;
  list-style: none;
  display: flex;
}

.single__task {
  min-height: 54px;
  border: 1px solid #bebcbc;
  border-radius: 4px;
  gap: 12px;
  display: flex;
}

.single__task__info {
  width: 76%;
  flex-flow: column wrap;
  gap: 12px;
  padding: 12px 0 12px 6px;
  display: flex;
}

.description {
  max-width: 400px;
}

.single__task__btns {
  width: 24%;
  align-items: center;
  gap: 8px;
  margin-right: 6px;
  display: flex;
}

.single__task__btns > * {
  border-radius: 4px;
  padding: 6px;
}

.delete__btn {
  color: #fff;
  background-color: #d86940;
}

.delete__btn:hover {
  cursor: pointer;
}

.check-container {
  width: 25px;
  height: 25px;
  display: block;
  position: relative;
}

.check-container input {
  opacity: 0;
  height: 0;
  width: 0;
  cursor: pointer;
  position: absolute;
}

.checkmark {
  height: 25px;
  width: 25px;
  content: "";
  background-color: #eee;
  border-radius: 4px;
  position: absolute;
  top: 0;
  left: 0;
}

.checkmark:hover {
  cursor: pointer;
}

.completed__true {
  text-decoration: line-through;
}

.check {
  background-color: green;
}

.check:before {
  height: 40%;
  width: 3px;
  content: "";
  transform-origin: 0 100%;
  background-color: #fff;
  position: absolute;
  top: 42%;
  left: 0;
  transform: translateX(10px)rotate(-45deg);
}

.check:after {
  height: 4px;
  width: 70%;
  content: "";
  transform-origin: 0 100%;
  background-color: #fff;
  position: absolute;
  bottom: 4px;
  left: 0;
  transform: translateX(10px)rotate(-45deg);
}

